.site-list {
  font-family: $ff-roboto-condensed;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;

  @include vp-767 {
    font-size: 16px;
  }
}

@include vp-767 {
  .site-list--servises {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

@include vp-767 {
  .site-list--abonements {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}

@include vp-767 {
  .site-list--actions {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}

@include vp-767 {
  .site-list--treners {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}

@include vp-767 {
  .site-list--shedules {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}

.site-list__link {
  padding: 5px 10px;
  cursor: pointer;

  &:hover {
    border-bottom: 1px solid $color-default-white;
  }

  &:focus {
    border: 1px solid $color-default-white;
    outline: none;
  }

  &:default {
    opacity: 0.4;
  }

  @include vp-767 {
    padding: 0;
  }
}
